.App {
  text-align: center;
}

.App-logo {
  height: 30px;
  pointer-events: none;
  
}
.splash-screen{

  height: 100vh;
  width: 100vw;
  position: absolute;
  background-color: rgba(0,0,0,1);
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  flex-direction: column;

}
.whiteText{
  color: white;
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  /* font-weight: bold; */
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.spacer{

  height: 130px;

}

.app-name{
    font-size: 4rem;
    text-align: center;
    font-weight: bolder;
    font-family: 'Agbalumo', Arial, Helvetica, sans-serif;

}

@media only screen and (max-width: 768px){

  .spacer{

    height: 220px;

}

}